
import React from 'react'
import { connect } from 'react-redux'
import { DataTable } from '@tencent/bere'
import "./style.css"
class DefinitionTable extends React.Component {
  render() {
    const {
      conditionTableData,
      updateTableData,
      loading,
      onSettingBtnClick
    } = this.props;
    const conditionTableColum = [{
      key: 'rule_field',
      title: '',
      render: (value, record) => {
        return (
          <div>
            <div><span class="text-overflow" title="=">{value.toUpperCase()}</span></div>
          </div>
        )
      }
    }, {
      key: 'op_type',
      title: '',
    }, {
      key: 'desc',
      title: '',
      render: (value, record) => {
        return (
          <div>
            <div><span class="text-overflow" title="=">{value.toUpperCase()}</span></div>
          </div>
        )
      }
    }, {
      key: 'setting',
      title: '',
      headerRender: () => {
        // return <td><div><span></span></div></td>
        return <span style={{ fontSize: '12px', color: '#444', lineHeight: '18px' }}></span>
      },
      render: (value, record) => {
        return (
          // <div>
          //   <a href="javascript:;"  onClick={() => { onSettingBtnClick('condition', 'edit', record) }}></a>
          //   <a href="javascript:;" onClick={() => { onSettingBtnClick('condition', 'delete', record) }}></a>
          // </div>
          <div>
            <a href="javascript:;" className="disable"></a>
            <a href="javascript:;" className="disable"></a>
          </div>
        )
      }
    }
    ];
    const updateTableColum = [{
      key: 'rule_field',
      title: '',
      render: (value, record) => {
        return (
          <div>
            <div><span class="text-overflow" title="=">{value.toUpperCase()}</span></div>
          </div>
        )
      }
    }, {
      key: 'desc',
      title: '',
      render: (value, record) => {
        return (
          <div>
            <div><span class="text-overflow" title="=">{value.toUpperCase()}</span></div>
          </div>
        )
      }
    }, {
      key: 'setting',
      title: '',
      headerRender: () => {
        // return <td><div><span></span></div></td>
        return <span style={{ fontSize: '12px', color: '#444', lineHeight: '18px' }}></span>
      },
      render: (value, record) => {
        return (
          // <div>
          //   <a href="javascript:;" onClick={() => { onSettingBtnClick('update', 'edit', record) }}></a>
          //   <a href="javascript:;" onClick={() => { onSettingBtnClick('update', 'delete', record) }}></a>
          // </div>
          <div>
            <a href="javascript:;" className="disable"></a>
            <a href="javascript:;" className="disable"></a>
          </div>
        )
      }
    }]

    return (
      <React.Fragment>
        <div className="param-box definal-table">
          <div className="param-hd">
            <h3></h3>
          </div>
          <div className="param-bd big-header">
            <DataTable
              columns={conditionTableColum}
              data={conditionTableData}
              rowKey="id"
              loading={loading}
            />
          </div>
        </div>
        <div className="param-box definal-table">
          <div className="param-hd">
            <h3></h3>
          </div>
          <div className="param-bd">
            <DataTable
              columns={updateTableColum}
              data={updateTableData}
              rowKey="id"
              loading={loading}
            />
          </div>
        </div>
      </React.Fragment >
    )
  }
}

export default connect(null)(DefinitionTable)
