
import React from 'react'
import { connect } from 'react-redux'

import Msg from '@components/Msg'
import { download } from '@utils/fetch'

class Upload extends React.Component {
  constructor(props) {
    super(props)
  }
  downloadHandler = () => {
    download(this.props.url,this.props.fileName)
  }

  render() {
    return (
      <React.Fragment>
        <Msg msg=''></Msg>
        <ul className="form-list">
          <li className="pure-text-row">
            <div className="form-input">
              <p className="form-text"> <a href="javascript:;" onClick={() => { this.downloadHandler() }} ></a></p>
            </div>
          </li>
          <li className="pure-text-row">
            <div className="form-input">
              <p className="form-text"></p>
            </div>
          </li>
          <li className="pure-text-row">
            <div className="form-input">
              <p className="form-text"></p>
            </div>
          </li>
          <li>
            <div className="form-input">
              <div className="form-unit form-upload is-success">
                <input type="text" className="tc-15-input-text m" readOnly placeholder={this.props.file ? this.props.file.name : ''} />
                <a href="javascript:void(0);" className="tc-upload-btn" title="">
                  <div className="file-input-wrap">
                    <input type="file" title=""
                      // accept={accept || 'aplication/zip'}
                      onChange={this.props.onAttachmentChange}
                    />
                    <span></span>
                  </div>
                </a>
                <p className="form-input-help">
                  20M
                  {/* {this.props.isOverSize ? '20M' : ''} */}
                </p>
              </div>
            </div>
          </li>
        </ul>
      </React.Fragment>
    )
  }
}


export default connect()(Upload)
