import React from 'react'
import {connect} from 'react-redux'
import {NavLink} from 'react-router-dom'
import {Select, TabList} from '@tencent/bere'

import Title from '@components/Title'
import Panel from '@layouts/PanelLayout.js'

import CodeTabPage from './CodeTabPage'
import DataAdjustPage from './DataAdjustPage'
import DataFilterPage from './DataFilterPage'
import GlassPage from './GlassPage'
import RulePage from './RulePage'

function eq(el,v){
  return el==v;
}

class App extends React.Component {
  componentDidMount() {
    this.props.dispatch({ type: 'image/fetchRuleInfos' })
    this.props.dispatch({
      type: 'image/change',
      payload: {
        addRulePageShow: false
      }
    })
  }
  handleDefectSelectChange = (val) => {
    this.props.dispatch({
      type: 'image/change',
      payload: {
        navSelect: val
      }
    })
  }
  handleSiteChange = site => {
    this.props.dispatch({
      type: 'image/change',
      payload: { site, shop: '', version: ''}
    })
  }
  handleShopChange = shop => {
    this.props.dispatch({
      type: 'image/change',
      payload: { shop: shop, version: ''}
    })
  }
  handleVersionChange = version => {
    const { site,shop} = this.props
    this.props.dispatch({
      type: 'image/change',
      payload: { version: version,ver_com_key:`${site}_${shop}_${version}`}
    })
  }
  render() {
    const { navSelect,site,shop,version,ruleInfos } = this.props
    const defectNavList = [
      {
        value: 'codeTab',
        text: 'Defect Code'
      },
      {
        value: 'dataAdjustTab',
        text: ''
      },
      {
        value: 'dataFilterTab',
        text: ''
      },
      {
        value: 'glassTab',
        text: 'Glass'
      }
    ]
    let content;
    if (navSelect === "codeTab") {
      content = <CodeTabPage />
    } else if (navSelect === "dataAdjustTab") {
      content = <DataAdjustPage />
    } else if (navSelect === "dataFilterTab") {
      content = <DataFilterPage />
    } else if (navSelect === "glassTab") {
      content = <GlassPage />
    }


    let siteList=[],shopList=[],versionList=[]
    for( let r of ruleInfos){
      if(r["site"] && !siteList.some(el=>el["value"]==r["site"])){
        siteList.push({text:r["site"],value:r["site"]})
      }
    }
    if(site){
      for(let r of ruleInfos){
        if(r["site"]==site){
          if(!shopList.some(el=>el["value"]==r["shop"])){
            shopList.push({text:r["shop"],value:r["shop"]})
          }
          if(shop && r["shop"]==shop && !versionList.some(el=>el["value"]==r["version"])){
            versionList.push({text:r["version"],value:r["version"]})
          }
        }
      }
    }

    return (
      <React.Fragment>
        <div className="main adc-manage-main">
          {
            this.props.addRulePageShow === true ?
              <RulePage></RulePage>
              :
              <div className='manage-area'>
                <Title title=''></Title>
                <div className="manage-area-main">
                  <div className='tc-15-tab tc-15-tab-alt tc-special-tab'>
                    <TabList
                      list={defectNavList}
                      value={navSelect}
                      onChange={this.handleDefectSelectChange}
                    />
                    <div className='tab-panel'>
                      <div className='manage-area-main-inner'>
                        <div>
                        <span style={{"color":"red"}}></span>
                          <Select
                            className="tc-15-select s"
                            value={site}
                            list={siteList}
                            selectFirstByDefault={false}
                            placeholder=""
                            onChange={this.handleSiteChange}
                          />
                          <Select
                            className="tc-15-select s"
                            value={shop}
                            list={shopList}
                            selectFirstByDefault={false}
                            placeholder=""
                            onChange={this.handleShopChange}
                            disabled={!site}
                          />
                          <Select
                            className="tc-15-select s"
                            value={version}
                            list={versionList}
                            selectFirstByDefault={false}
                            placeholder=""
                            onChange={this.handleVersionChange}
                            disabled={!shop}
                          />
                        </div>
                        <div>&nbsp;</div>
                        <Panel>
                          {content}
                        </Panel>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
          }
        </div>
      </React.Fragment>
    )
  }
}

const mapStateToProps = ({ image }) => {
  return {
    addRulePageShow: image.addRulePageShow,
    navSelect: image.navSelect,
    ruleInfos:image.ruleInfos,
    site:image.site,
    shop:image.shop,
    version:image.version
  }
}
export default connect(mapStateToProps)(App)
