import React from 'react'
import { connect } from 'react-redux'
import RuleEdit from '@components/RuleEdit'

class RulePage extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      loading: false
    }
  }
  onCancel = () => {
    this.props.dispatch({
      type: 'image/change',
      payload: {
        addRulePageShow: false
      }
    })
  }
  handleAddRuleOk = (data,relate_id) => {
    this.setState({
      loading: true
    })
    let callback = (error) => {
      this.setState({
        loading: false
      })
      if (error) {
        return
      }
      this.onCancel()
    }
    if (this.props.addRulePageIsEdit) {
      this.props.dispatch({
        type: "image/editRule",
        payload: ({
          data: data,
          relate_id: relate_id,
          id: this.props.editColumData.id,
          type: this.props.addRulePageType
        }),
        callback
      })
    } else {
      this.props.dispatch({
        type: "image/addRule",
        payload: ({
          data: data,
          relate_id: relate_id,
          type: this.props.addRulePageType
        }),
        callback
      })
    }
  }
  handleNavSelectChange = (val) => {
    this.props.dispatch({
      type: 'image/change',
      payload: {
        navSelect: val
      }
    })
  }
  render() {
    return (
      <React.Fragment>
        <RuleEdit
          loading={this.state.loading}
          onCancel={this.onCancel}
          onOk={this.handleAddRuleOk}
          ruleColum={this.props.addRulePageColum}
          isEdit={this.props.addRulePageIsEdit}
          relate_id={this.props.addRulePageIsEdit?this.props.editColumData["relate_id"]:0}
          editColumData={this.props.editColumData}
          addRulePageType={this.props.addRulePageType}
        />
      </React.Fragment>
    )
  }
}

const mapStateToProps = ({ image }) => {
  return {
    addRulePageColum: image.addRulePageColum,
    addRulePageIsEdit: image.addRulePageIsEdit,
    editColumData: image.editColumData,
    addRulePageType:image.addRulePageType
  }
}

export default connect(mapStateToProps)(RulePage)
