
import React from 'react'
import { connect } from 'react-redux'
import { TabList, notification } from '@tencent/bere'

import CreatDialog from '@components/CreatDialog'
import BtnGroup from '@components/BtnGroup'
import DefineTable from '@components/DefineTable'
import InputTable from '@components/InputTable'
import UploadDialog from '@components/Upload'
import DeleteDialog from '@components/DeleteDialog'

class DataFilterPage extends React.Component {
  constructor(props) {
    super(props)
    this.state = ({
      uplodVisable: false
    })
  }
  componentDidMount() {
    this.fetchData()
  }
  componentWillReceiveProps (nextProps) {
    if(nextProps.ver_com_key!=this.props.ver_com_key){
      console.log("ver_com_key had change")
      this.fetchData()
    }
  }
  //
  fetchData = (val) => {
    let select = val || this.props.dataFilterPage.navSelect;
    if (select === 'define') {
      this.props.dispatch({
        type: "image/fetchRuleDefinalTableInFilterPage"
      })
    } else {
      this.props.dispatch({
        type: "image/fetchRuleTableInFilterPage"
      })
    }
  }
  //
  handleCreatBtnClick = () => {
    if (this.props.dataFilterPage.navSelect === "define") {
      this.props.dispatch({
        type: 'image/change',
        payload: {
          creatModalShow: {
            visible: true,
            isEdit: false,
            initialValues: {
              ruleType: 'condition'
            }
          }
        }
      })
    } else {
      this.props.dispatch({
        type: 'image/change',
        payload: {
          addRulePageShow: true,
          addRulePageColum: this.props.dataFilterPage.ruleTableColum,
          addRulePageIsEdit: false,
          editColumData: null,
          addRulePageType: 3,
        }
      })
    }
  }
  //
  handleBlukEditBtnClick = () => {
    this.setState({ uplodVisable: true })
  }
  //defineTable /
  handleDefineSettingBtnClick = (type, setting, record) => {
    if (setting === "edit") {
      this.props.dispatch({
        type: 'image/change',
        payload: {
          creatModalShow: {
            visible: true,
            isEdit: true,
            initialValues: {
              ruleType: type,
              field: record.field,
              op_type: record.op_type,
              name: record.name,
              desc: record.desc
            }
          }
        }
      })
    } else {
      DeleteDialog.open({
        confirmCallBack(startLoading, endLoading) {
          startLoading()
          setTimeout(() => {
            endLoading()
            DeleteDialog.close()
          }, 1000)
        },
        cancelCallBack() {
          DeleteDialog.close()
        }
      })
    }
  }
  //InputTable /
  handleInputSettingBtnClick = (setting, record) => {
    if (setting === "edit") {
      this.props.dispatch({
        type: 'image/change',
        payload: {
          addRulePageShow: true,
          addRulePageColum: this.props.dataFilterPage.ruleTableColum,
          addRulePageIsEdit: true,
          editColumData: record,
          addRulePageType: 3
        }
      })
    } else {
      let that = this;
      DeleteDialog.open({
        confirmCallBack(startLoading, endLoading) {
          startLoading()
          that.props.dispatch({
            type: "image/deleteRule",
            payload: {
              id: record.id,
              type: 3
            },
            callback() {
              that.fetchData()
              endLoading()
              DeleteDialog.close()
            },
          })
        },
        cancelCallBack() {
          DeleteDialog.close()
        }
      })
    }
  }
  //modal ok
  handleCreatModalOk = (data) => {
    if (data.type === 'condition') {
      if (!data.type || !data.desc || !data.op_type) {
        notification.error('!', 2000)
        return
      }

    }

    this.handleCreatModalCancel()
  }
  //modal
  handleCreatModalCancel = () => {

    this.props.dispatch({
      type: 'image/change',
      payload: {
        creatModalShow: {
          visible: false,
          isEdit: false,
          initialValues: {
            ruleType: 'condition'
          }
        }
      }
    })
  }
  ///
  handleRuleSelectChange = (val) => {
    this.props.dispatch({
      type: 'image/changeDataFilterPage',
      payload: {
        navSelect: val
      }
    })
    this.fetchData(val)
  }
  //
  onPerPageChange = (val) => {
    this.props.dispatch({
      type: 'image/changePage',
      payload: {
        ruleTablePageCount: val,
        type: 3,
      },
      callback: () => {
        this.fetchData()
      }
    })
  }
  //
  onPageChange = (val) => {
    this.props.dispatch({
      type: 'image/changePage',
      payload: {
        ruleTablePageCurrent: val,
        type: 3,
      },
      callback: () => {
        this.fetchData()
      }
    })
  }
  render() {
    const {
      dataFilterPage,
      site,
      shop,
      version
    } = this.props;
    const ruleNavList = [
      {
        value: 'define',
        text: ''
      },
      {
        value: 'input',
        text: ''
      }
    ]

    return (
      <React.Fragment>
        <TabList
          list={ruleNavList}
          value={dataFilterPage.navSelect}
          onChange={this.handleRuleSelectChange}
        />
        <div className='tab-panel'>
          <BtnGroup
            disabled={(dataFilterPage.navSelect === 'define' ? true : !(site&&shop&&version))}
            creatFncHandle={() => this.handleCreatBtnClick()}
            editFncHandle={() => this.handleBlukEditBtnClick()}
          />
          {
            dataFilterPage.navSelect === 'define' ?
              <DefineTable
                conditionTableData={dataFilterPage.conditionTableData}
                updateTableData={dataFilterPage.updateTableData}
                loading={dataFilterPage.definitionTableLoading}
                onSettingBtnClick={this.handleDefineSettingBtnClick}
              /> :
              <InputTable
                loading={dataFilterPage.ruleTableLoading}
                ruleTableColum={dataFilterPage.ruleTableColum}
                ruleTableData={dataFilterPage.ruleTableData}
                ruleTablePageTotal={dataFilterPage.ruleTablePageTotal}
                ruleTablePageCurrent={dataFilterPage.ruleTablePageCurrent}
                ruleTablePageCount={dataFilterPage.ruleTablePageCount}
                handleSettingBtnClick={this.handleInputSettingBtnClick}
                onPerPageChange={this.onPerPageChange}
                onPageChange={this.onPageChange}
              ></InputTable>
          }
        </div>
        <CreatDialog
          visible={this.props.visible}
          isEdit={this.props.isEdit}
          initialValues={this.props.initialValues}
          onOk={this.handleCreatModalOk}
          onCancel={this.handleCreatModalCancel}
        ></CreatDialog>
        <UploadDialog
          type={3}
          site={site}
          version={version}
          shop={shop}
          visible={this.state.uplodVisable}
          onCancel={() => {
            this.setState({
              uplodVisable: false
            })
            this.fetchData()
          }}
        ></UploadDialog>
      </React.Fragment>
    )
  }
}

const mapStateToProps = ({ image }) => {
  const { creatModalShow: { visible, isEdit, initialValues } } = image;

  return {
    dataFilterPage: image.dataFilterPage,
    site:image.site,
    shop:image.shop,
    version:image.version,
    ver_com_key:image.ver_com_key,

    visible: visible,
    isEdit: isEdit,
    initialValues: {
      type: initialValues.ruleType || 'condition',
      field: initialValues.field || '',
      op_type: initialValues.op_type || '=',
      name: initialValues.name || '',
      desc: initialValues.desc || ''
    }
  }
}

export default connect(mapStateToProps)(DataFilterPage)
